import  React,{Suspense} from "react";
import './App.css';
import Layout from "./Components/Layout/Layout";
import { Route,Switch,Redirect } from "react-router-dom";

import LoadingSpinner from './Components/UI/LoadingSpinner';


const Quotes=React.lazy(()=>import('./Components/Pages/Quotes'));
const NewQuote=React.lazy(()=>import('./Components/Pages/NewQuote'));
const QuoteDetail=React.lazy(()=>import('./Components/Pages/QuoteDetails'));
const NotFound=React.lazy(()=>import('./Components/Pages/NotFound'));

function App() {
  return (
   
      <Layout>
      <Suspense  fallback={<div className={'cenetered'}><LoadingSpinner/></div>}>
        <Switch>
        <Route path="/" exact>
          <Redirect to="/quotes" />
        </Route>
        <Route path="/new-quote" exact>
          <NewQuote/>
          </Route>
          <Route path="/quotes/:quoteId">
          <QuoteDetail />
        </Route>
          <Route path="/quotes" exact>
              <Quotes/>
          </Route>
        <Route path='*'>
            <NotFound/>
        </Route>
        </Switch>
        </Suspense>
      </Layout>
     
   
    
  );
}

export default App;
