

import { NavLink } from 'react-router-dom';


const MainNavigation = () => {
  return (
    <nav
      className='navbar navbar-expand-lg navbar-dark MainNavig'
      style={{
      background: 'linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 0%, rgba(246,252,252,1) 0%, rgba(229,237,247,1) 0%, rgba(107,191,208,1) 0%, rgba(188,224,232,1) 0%, rgba(96,215,240,1) 0%, rgba(27,94,108,1) 0%, rgba(47,151,172,1) 100%)'
      }}>
      <NavLink
        className='navbar-brand'
        to='/quotes'
        style={{ fontSize: '1.6em', marginLeft: '1.1em',padding:'0px' }}>
        <img src={process.env.PUBLIC_URL + '/logo.png'} alt='logo' style={{ width: '60px',margin:'0px' }} />
      </NavLink>
      <button
        className='navbar-toggler'
        type='button'
        data-toggle='collapse'
        data-target='#navbarNav'
        aria-controls='navbarNav'
        aria-expanded='false'
        aria-label='Toggle navigation'>
        <span className='navbar-toggler-icon'></span>
      </button>
      <div
        className='collapse navbar-collapse justify-content-end'
        id='navbarNav'>
        <ul className='navbar-nav' style={{ marginRight: '5em' }}>
          <li className='nav-item' style={{ paddingRight: '2em' }}>
            <NavLink
              className='nav-link '
              to='/quotes'
              style={{ fontSize: '1.2em' }}>
              <b>Quotes{' '}</b>
            </NavLink>
          </li>
          <li className='nav-item'>
            <NavLink
              className='nav-link'
              to='/new-quote'
              style={{ fontSize: '1.2em' }}>
              <b>Add Quote</b>
            </NavLink>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default MainNavigation;
